.big-advantages {
	padding: 40px 0;
	text-align: center;
	color: $dark;
	.container {
		max-width: 1390px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.inner {
		margin: 60px 0 0 0;
		@include flex(row, null, null, wrap);
		@include mq($max-width: 1200px) {
			margin: 25px 0 0 0; } }
	.item {
		background: white;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.06);
		border-radius: 10px;
		text-align: left;
		position: relative;
		width: 31.3%;
		margin: 1%;
		padding: 56px 40px;
		@include mq($max-width: 1000px) {
			width: 48%; }
		@include mq($max-width: 700px) {
			padding: 40px 20px; }
		@include mq($max-width: 400px) {
			width: 100%; }
		&::before {
			content: '';
			border-radius: 10px;
			left: 20px;
			right: 20px;
			top: 20px;
			bottom: 20px;
			border: 1px solid #CCCCCC;
			position: absolute;
			@include mq($max-width: 700px) {
				left: 10px;
				top: 10px;
				right: 10px;
				bottom: 10px; } }
		&__circle {
			width: 62px;
			height: 62px;
			@include flex(row, center, center, nowrap);
			background: linear-gradient(218.13deg, #CC9A1B 17.16%, #EEB628 85.07%);
			font: 700 32px/1 'PF', sans-serif;
			font-style: italic;
			color: white;
			border-radius: 100%;
			@include mq($max-width: 1200px) {
				font-size: 24px;
				width: 40px;
				height: 40px;
				position: absolute;
				left: 0;
				top: 0;
				border-radius: 10px 50% 50%; } }
		&-title {
			@include flex(row, null, center, wrap); }
		&__title {
			font-family: 'PF', serif;
			color: $yellow;
			padding: 0 0 0 20px;
			font-weight: 500;
			font-style: italic;
			font-size: 25px;
			width: calc( 100% - 82px );
			@include mq($max-width: 1200px) {
				width: 100%;
				padding: 0;
				font-size: 20px; } }
		&__text {
			margin: 20px 0 0 0;
			font: 18px/1.6 'UniBook', sans-serif;
			@include mq($max-width: 700px) {
				margin: 10px 0 0 0;
				font-size: 14px; } } } }



