.proffesional-icons {
	padding: 45px 0;
	.container {
		max-width: 1360px; }
	.inner {
		margin: 45px 0 0 0;
		@include flex(row, between, null, wrap); }
	.block-title {
		text-align: center;
		padding: 0 15px;
		margin: 0 0 30px 0; }
	.item {
		@include flex(column, null, center, nowrap);
		width: 20%;
		text-align: center;
		@include mq($max-width: 575px) {
			max-width: 48%;
			margin: 2% 1%; }
		.img-wrap {
			width: 98px;
			height: 98px;
			border-radius: 100%;
			background: white;
			box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
			@include flex(row, center, center, nowrap); }
		&__title {
			margin: 20px 0;
			font-weight: 700;
			font-size: 18px;
			min-height: 48px;
			@include mq($max-width: 575px) {
				margin: 10px 0;
				font-size: 15px; } }
		&__text {
			font-size: 18px;
			@include mq($max-width: 575px) {
				font-size: 13px; } } } }




