.frequent-problems {
	padding: 80px 0 40px 0;
	@include mq($max-width: 1200px) {
		padding: 40px 0; }
	.container {
		max-width: 1460px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	&-price {
		text-align: right;
		margin: 0 0 20px 0;
		&__text {
			font-size: 20px;
			margin: 0 0 10px 0;
			color: $yellow2;
			b {
				font-size: 32px; } } }
	.tabs {
		@include flex(row, between, start, wrap);
		@include mq($max-width: 1000px) {
			flex-wrap: wrap-reverse; }
		&-card {
			padding: 40px;
			width: 82%;
			background: #FFFFFF;
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
			border-radius: 10px;
			position: relative;
			z-index: 2;
			display: none;
			@include mq($max-width: 1000px) {
				width: 100%;
				padding: 15px; }
			&.active {
				display: block; }
			&::after {
				content: '';
				clear: both; }
			&__img {
				display: block;
				max-width: 480px;
				width: 100%;
				margin: 0 28px 28px 0;
				float: left;
				@include mq($max-width: 1600px) {
					max-width: none;
					width: 40%; }
				@include mq($max-width: 575px) {
					float: none;
					width: 100%;
					margin: 0 0 15px 0; } } }
		&-list {
			padding: 50px 0 0 0;
			width: 18%;
			position: relative;
			z-index: 1;
			@include mq($max-width: 1000px) {
				width: 100%;
				@include flex(row, null, null, nowrap);
				overflow-x: scroll;
				background: white;
				padding: 0;
				margin: 0 0 15px 0;
				border-radius: 10px;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
				@include scrollbar(auto, 4px, $yellow, $gray, black, null); }
			&-item {
				margin: 15px 0;
				@include mq($max-width: 1000px) {
					margin: 0 0 5px;
					min-width: 200px;
					display: flex; }
				@include mq($max-width: 575px) {
					min-width: 150px; } }
			&__link {
				background: #FFFFFF;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
				border-radius: 0 10px 10px 0;
				min-height: 70px;
				padding: 0 20px;
				@include flex(row, null, center, nowrap);
				color: #A3A9B0;
				font-weight: 500;
				font-size: 20px;
				@include mq($max-width: 1000px) {
					font-size: 18px;
					background: none;
					box-shadow: none;
					text-align: center;
					width: 100%;
					display: block;
					min-height: 0;
					padding: 20px 0; }
				&.active {
					color: $brown;
					text-decoration: underline; }
				@include mq($min-width: 1200px) {
					&:hover {
						color: $brown;
						text-decoration: underline; } } } } }
	.block-title {
		margin: 0 0 30px 0;
		span {
			color: $brown; } }
	.text-container {
		ol {
			column-count: 2;
			@include mq($max-width: 575px) {
				column-count: 1; } } } }
