.category-about {
	padding: 50px 0 35px;
	@include mq($max-width: 1200px) {
		padding: 30px 15px; }
	.container {
		max-width: 1430px;
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		border-radius: 10px;
		padding: 0;
		@include flex(row, between, null, wrap);
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	&-left {
		width: 45%;
		overflow: hidden;
		border-radius: 10px 0 0 10px;
		@include mq($max-width: 700px) {
			width: 100%;
			border-radius: 10px; } }
	&-right {
		padding: 20px;
		width: 53%;
		@include mq($max-width: 700px) {
			width: 100%; } }
	&__img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover; }
	.text-container {
		margin: 25px 0 0 0;
		overflow: auto; } }



