.orthodontics-stages {
	padding: 45px 0;
	.container {
		max-width: 1570px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.block-title {
		text-align: center; }
	.block-text {
		text-align: center;
		margin: 15px 0; }
	.inner {
		@include flex(row, null, null, wrap); }
	.card {
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		border-radius: 10px;
		width: 29.3%;
		margin: 2%;
		padding: 35px;
		@include mq($max-width: 1600px) {
			padding: 15px;
			width: 31.3%;
			margin: 1%; }
		@include mq($max-width: 1000px) {
			width: 48%; }
		@include mq($max-width: 575px) {
			width: 100%; }
		&__img {
			display: block;
			width: 100%;
			margin: 20px 0;
			@include mq($max-width: 575px) {
				margin: 10px 0; } }
		&__title {
			font-size: 24px;
			font-weight: 700;
			@include mq($max-width: 1600px) {
				font-size: 20px; }
			@include mq($max-width: 575px) {
				font-size: 18px; } }
		&__text {
			font-size: 18px;
			margin: 10px 0;
			@include mq($max-width: 1600px) {
				font-size: 16px; }
			@include mq($max-width: 575px) {
				font-size: 14px; }
			&_small {
				font-weight: 300; } } } }



