.infoblock {
	padding: 45px 0;
	.container {
		max-width: 1520px;
		@include flex(row, between, center, wrap);
		@include mq($max-width: 1600px) {
			max-width: 1170px; }
		@include mq($max-width: 1000px) {
			flex-wrap: wrap-reverse; } }
	&__img {
		display: block;
		width: 49%;
		@include mq($max-width: 1000px) {
			width: 100%; } }
	.inner {
		width: 49%;
		@include mq($max-width: 1000px) {
			width: 100%;
			margin: 0 0 25px 0; } }
	.block-text {
		margin: 40px 0;
		@include mq($max-width: 1600px) {
			margin: 20px 0; } } }
