.doctor-quote {
	padding: 100px 0;
	@include mq($max-width: 1200px) {
		padding: 50px 15px; }
	@include mq($max-width: 575px) {
		padding: 30px 0; }
	.container {
		background: white;
		border-radius: 10px;
		max-width: 1520px;
		position: relative;
		@include mq($max-width: 1600px) {
			max-width: 1170px; }
		@include mq($max-width: 1000px) {
			background: none;
			display: flex;
			flex-wrap: wrap-reverse; } }
	&-left {
		padding: 40px 0 40px 105px;
		width: calc( 100% - 520px );
		@include mq($max-width: 1600px) {
			padding: 20px 0 20px 45px;
			width: calc( 100% - 420px ); }
		@include mq($max-width: 1000px) {
			padding: 20px;
			width: 100%;
			background: white; } }
	&-right {
		position: absolute;
		top: 50%;
		right: 0;
		z-index: 2;
		transform: translate(0, -50%);
		background: white;
		border-radius: 10px;
		padding: 22px;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		max-width: 470px;
		width: 100%;
		@include mq($max-width: 1600px) {
			max-width: 400px; }
		@include mq($max-width: 1000px) {
			margin: 0 0 20px;
			position: static;
			transform: translate(0, 0); }
		@include mq($max-width: 575px) {
			width: 100%;
			padding: 10px;
			border-radius: 10px; } }
	&__quote {
		color: #F2F2F2;
		font-weight: 900;
		font-size: 176px;
		line-height: .7;
		font-style: italic;
		@include mq($max-width: 575px) {
			font-size: 100px; } }
	&__img {
		display: block;
		width: 100%; }
	.block-title {
		font-weight: 500;
		span {
			color: $brown; } }
	.block-text {
		margin: 15px 0; }
	&__name {
		text-align: right;
		font-family: 'PF', serif;
		font-style: italic;
		font-weight: 500;
		font-size: 24px;
		@include mq($max-width: 575px) {
			font-size: 20px; } }
	&__sign {
		display: block;
		margin: 15px 0 0 auto; } }
