.setting-indication {
	padding: 40px 0;
	@include mq($max-width: 1600px) {
		padding: 20px 0; }
	.container {
		max-width: 1430px;
		@include mq($max-width: 1600px) {
			max-width: 1200px; } }
	.block-title {
		text-align: center; }
	.inner {
		margin: 40px 0 0 0;
		@include flex(row, between, start, wrap); }
	.item {
		width: 23%;
		margin: 0 1%;
		text-align: center;
		color: $dark;
		@include mq($max-width: 1200px) {
			width: 48%;
			margin: 1%; }
		@include mq($max-width: 575px) {
			width: 100%;
			margin: 1% 0; }
		&__img {
			width: 100%;
			display: block;
			border-radius: 10px; }
		&__title {
			margin: 20px 0;
			font: italic 23px/1 'PF', sans-serif;
			@include mq($max-width: 1600px) {
				font-size: 20px;
				margin: 15px 0; }
			@include mq($max-width: 575px) {
				font-size: 16px;
				margin: 10px 0; } }
		&__text {
			font-family: 'UniBook', sans-serif;
			font-size: 16px;
			@include mq($max-width: 1600px) {
				font-size: 14px; } } } }



