.included {
	padding: 100px 0;
	text-align: center;
	color: $dark;
	@include bg(null, null, 'included/tape.png', no-repeat, center, center, contain);
	@include mq($max-width: 1600px) {
		padding: 50px 0; }
	@include mq($max-width: 575px) {
		padding: 25px 0; }
	.container {
		max-width: 1390px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.inner {
		margin: 30px 0 0 0;
		@include flex(row, null, null, wrap); }
	.item {
		background: white;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.06);
		border-radius: 10px;
		text-align: left;
		position: relative;
		width: 31.3%;
		margin: 1%;
		padding: 56px 40px;
		@include mq($max-width: 1000px) {
			width: 48%; }
		@include mq($max-width: 700px) {
			padding: 40px 20px; }
		@include mq($max-width: 400px) {
			width: 100%; }
		&::before {
			content: '';
			border-radius: 10px;
			left: 20px;
			right: 20px;
			top: 20px;
			bottom: 20px;
			border: 1px solid #CCCCCC;
			position: absolute;
			@include mq($max-width: 700px) {
				left: 10px;
				right: 10px;
				bottom: 10px;
				top: 10px; } }
		&__title {
			font-family: 'PF', serif;
			padding: 0 0 0 20px;
			font-weight: 500;
			font-style: italic;
			font-size: 25px;
			@include mq($max-width: 1600px) {
				padding: 0; }
			@include mq($max-width: 700px) {
				font-size: 20px; } }
		&__text {
			margin: 20px 0 0 0;
			font: 18px/1.6 'UniBook', sans-serif;
			@include mq($max-width: 700px) {
				margin: 15px 0 0 0;
				font-size: 14px; } } } }
