.choose-prosthetics {
	padding: 65px 0;
	@include mq($max-width: 1200px) {
		padding: 30px 0; }
	.container {
		max-width: 1600px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.block-title {
		text-align: center; }
	.inner {
		margin: 40px 0 0 0;
		@include flex(row, null, null, wrap); }
	.card {
		padding: 35px;
		border-radius: 10px;
		background: white;
		margin: 1%;
		@include mq($max-width: 1600px) {
			padding: 15px; }
		&_middle {
			width: 30%;
			@include mq($max-width: 1200px) {
				width: 48%; }
			@include mq($max-width: 575px) {
				width: 100%; } }
		&_small {
			width: 22%;
			@include mq($max-width: 1200px) {
				width: 48%; }
			@include mq($max-width: 575px) {
				width: 100%; } }
		&_big {
			width: 42%;
			@include mq($max-width: 1200px) {
				width: 100%; }
			.card-list {
				column-count: 2;
				@include mq($max-width: 575px) {
					column-count: 1; } } }
		&__title {
			font-size: 24px;
			font-weight: 700;
			@include mq($max-width: 1600px) {
				font-size: 20px; }
			@include mq($max-width: 575px) {
				font-size: 17px; } }
		&__text {
			font-size: 17px;
			margin: 5px 0 0 0;
			@include mq($max-width: 1600px) {
				font-size: 15px; } }
		&-list {
			margin: 38px 0 0 0;
			font-size: 17px;
			@include mq($max-width: 1600px) {
				font-size: 15px;
				margin: 15px 0 0 0; }
			@include mq($max-width: 575px) {
				font-size: 13px; }
			li {
				margin: 0 0 30px 0;
				padding: 0 0 0 20px;
				position: relative;
				@include mq($max-width: 1600px) {
					margin: 0 0 20px 0; }
				@include mq($max-width: 575px) {
					margin: 0 0 10px 0; }
				&:before {
					content: '';
					display: block;
					position: absolute;
					background: $brown;
					width: 12px;
					height: 12px;
					border-radius: 100%;
					left: 0;
					top: 6px; } } } } }


