.our-priority {
	padding: 50px 0;
	.container {
		max-width: 1400px;
		@include flex(row, between, center, wrap);
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.block-title {
		margin: 0 0 15px 0;
		span {
			display: block;
			width: 100%;
			text-align: right; } }
	&-left {
		width: 45%;
		@include mq($max-width: 700px) {
			width: 100%;
			margin: 0 0 20px 0; } }
	&-right {
		width: 52%;
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		border-radius: 10px;
		padding: 27px;
		@include mq($max-width: 700px) {
			width: 100%;
			padding: 10px; } }
	&__img {
		display: block;
		width: 100%;
		height: auto; }
	.action {
		margin: 80px 0 0 0;
		@include mq($max-width: 1000px) {
			margin: 30px 0 0 0; } } }
