.portfolio {
	padding: 75px 0;
	color: $dark2;
	@include mq($max-width: 1600px) {
		padding: 50px 0; }
	@include mq($max-width: 575px) {
		padding: 25px 0; }
	.container {
		max-width: 1460px;
		@include mq($max-width: 1600px) {
			max-width: 1170px; } }
	.slider {
		background: #FFFFFF;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.15);
		position: relative; }
	.block-title {
		text-align: center; }
	.slider {
		margin: 35px 0 0 0; }
	.twentytwenty-wrapper {
		width: 40%;
		@include mq($max-width: 1000px) {
			width: 60%;
			margin: 0 0 15px; }
		@include mq($max-width: 700px) {
			width: 100%; } }
	.slide {
		padding: 55px;
		@include mq($max-width: 1000px) {
			padding: 20px 20px 50px 20px; }
		&-top {
			width: 100%;
			@include flex(row, between, start, nowrap);
			@include mq($max-width: 1000px) {
				flex-wrap: wrap; }
			&-left {
				width: 60%;
				padding: 0 20px 0 0;
				@include mq($max-width: 1000px) {
					width: 100%; } }
			&-right {
				width: 100%; } }
		&-bottom {
			width: 100%;
			@include flex(row, between, center, nowrap);
			@include mq($max-width: 700px) {
				flex-wrap: wrap; } }
		&__name {
			font: 500 32px/1 'PF', sans-serif;
			width: 100%;
			font-style: italic;
			@include mq($max-width: 575px) {
				font-size: 24px; } }
		&__text {
			width: 100%;
			margin: 0 0 25px 0;
			font: 400 18px/1.6 'UniBook', sans-serif;
			@include mq($max-width: 1600px) {
				font-size: 15px; }
			@include mq($max-width: 575px) {
				font-size: 14px; } }
		&__doc {
			font: 400 16px/1 'UniBook', sans-serif;
			@include mq($max-width: 700px) {
				margin: 0 0 10px 0; }
			@include mq($max-width: 575px) {
				width: 100%;
				font-size: 14px; }
			span {
				display: inline-block;
				margin: 0;
				font: 500 20px/1 'PF', sans-serif;
				@include mq($max-width: 575px) {
					font-size: 16px; } } } }

	.arrow {
		position: absolute;
		top: 50%;
		z-index: 2;
		@include mq($max-width: 1200px) {
			display: flex; }
		&-prev {
			left: 0;
			transform: translate(-50%, -50%) rotate(180deg);
			@include mq($max-width: 1200px) {
				transform: translate(0, 0) rotate(180deg);
				top: auto;
				bottom: 5px;
				left: 5px; } }
		&-next {
			right: 0;
			transform: translate(50%, -50%);
			@include mq($max-width: 1200px) {
				transform: translate(0, 0);
				top: auto;
				bottom: 5px;
				right: 5px; } } } }


